import { Directive, OnDestroy, OnInit } from '@angular/core';
import { JmartAuthService } from '@jmart-cabinet/services/jmart-auth.service';
import { fromEvent, interval, merge, Observable, of, Subscription } from 'rxjs';
import { debounce } from 'rxjs/operators';

@Directive()
export abstract class AbstractJmartCabinetLayout implements OnInit, OnDestroy {
  constructor(protected authService: JmartAuthService) {}

  userActivitySub: Subscription;

  ngOnInit(): void {
    const events = [
      'scroll',
      'wheel',
      'touchmove',
      'touchend',
      'mousemove',
      'mouseup',
      'mousedown',
    ];

    const eventStreams = events.map((ev) => fromEvent(document, ev));
    const allEvents$ = merge(...eventStreams);

    this.userActivitySub = allEvents$
      .pipe(debounce((ev) => interval(60000)))
      .subscribe(() => {
        this.authService.checkLastUserActivityTime();
      });

    if (this.authService.isUserLogged()) {
      this.authService.refresh();
    }
  }

  ngOnDestroy(): void {
    this.userActivitySub.unsubscribe();
  }
}
