import { AnalyticsManagerAbstract } from '@analytics/services/manager';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { JmartAuthService } from '@jmart-cabinet/services/jmart-auth.service';

@Component({
  selector: 'j-jmart-auth-form',
  templateUrl: './jmart-auth-form.component.html',
  styleUrls: ['./jmart-auth-form.component.scss']
})
export class JmartAuthFormComponent implements OnInit {

  constructor(
    private authService: JmartAuthService,
    private router: Router,
    private analytics: AnalyticsManagerAbstract,
  ) { }

  ngOnInit(): void {
    const refreshSub = this.authService.refreshSubject$.subscribe(
      () => {
        this.router.navigate(['jmart-cabinet', 'products',]);
        refreshSub.unsubscribe();
      }
    );
    if (this.authService.isUserLogged()) {
      this.authService.refresh();
    }
    this.analytics.logEvent('JMartSingleAut_Start', {
      version: 1,
      type: 'direct',
    });
  }

  isLoading = false;
  hasError = false;

  passwordRegExp = /[^ ]/;
  passwordPasteRegExp = /[ ]/;

  authForm = new FormGroup({
    login: new FormControl('', [Validators.required]),
    password: new FormControl('', [Validators.required]),
  });

  onSubmit() {
    if (this.authForm.valid) {
      this.isLoading = true;
      this.hasError = false;
      
      this.authService.auth(
        this.authForm.value,
        { 
          error: () => {
            this.hasError = true;
          }
        }
      ).add(() => this.isLoading = false);

      this.analytics.logEvent('JMartSingleAut_Enter_Click', {
        version: 1,
      });
    }
  }


}
