import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree, Router } from '@angular/router';
import { JmartAuthService } from '@jmart-cabinet/services/jmart-auth.service';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AuthenticatedToJmartCabinetGuard implements CanActivate {

  constructor(
    private jmartAuthService: JmartAuthService,
    private router: Router,
  ) {}

  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    if (Boolean(this.jmartAuthService.jmartCabinetAuthData.tokens)) {
      return true;
    }
    this.router.navigate(['jmart-cabinet', 'auth']);
    return false;

    // return Boolean(this.jmartAuthService.jmartCabinetAuthData.token);
  }
  
}
