import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { JmartAuthFormComponent } from './components/jmart-auth-form/jmart-auth-form.component';
import { JmartCabinetLayoutComponent } from './components/jmart-cabinet-layout/jmart-cabinet-layout.component';
import { AuthenticatedToJmartCabinetGuard } from './guards/authenticated-to-jmart-cabinet.guard';


const routes: Routes = [
  {
    path: '',
    canActivate: [AuthenticatedToJmartCabinetGuard],
    component: JmartCabinetLayoutComponent,
  },
  {
    path: 'auth',
    component: JmartAuthFormComponent,
  },
  {
    path: 'settings',
    canActivate: [AuthenticatedToJmartCabinetGuard],
    loadChildren: () => import('./modules/settings/settings.module').then((m) => m.SettingsModule),
  },
  {
    path: 'products',
    canActivate: [AuthenticatedToJmartCabinetGuard],
    loadChildren: () => import('./modules/products/products.module').then((m) => m.ProductsModule)
  },
  {
    path: 'tariff',
    canActivate: [AuthenticatedToJmartCabinetGuard],
    loadChildren: () => import('./modules/tariff/tariff.module').then((m) => m.TariffModule)
  },
  {
    path: 'account-statement',
    canActivate: [AuthenticatedToJmartCabinetGuard],
    loadChildren: () => import('./modules/account-statement/account-statement.module').then((m) => m.AccountStatementModule)
  },
  {
    path: 'product-card',
    canActivate: [AuthenticatedToJmartCabinetGuard],
    loadChildren: () =>
      import('./modules/products/pages/card-product-page/card-product-page.module').then((m) => m.CardProductPageModule),
  },
  {
    path: 'orders',
    canActivate: [AuthenticatedToJmartCabinetGuard],
    loadChildren: () => import('./modules/orders/orders.module').then((m) => m.OrdersModule)
  },
  {
    path: 'order-card',
    canActivate: [AuthenticatedToJmartCabinetGuard],
    loadChildren: () =>
      import('./modules/orders/pages/card-order-page/card-order-page.module').then((m) => m.CardOrderPageModule),
  },
  {
    path: '**',
    redirectTo: 'settings',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class JmartCabinetRoutingModule { }
