import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { Router } from '@angular/router';
import { AbstractJmartCabinetLayout } from '@jmart-cabinet/classes/abstract-jmart-cabinet-layout.class';
import { JmartAuthService } from '@jmart-cabinet/services/jmart-auth.service';

@Component({
  selector: 'j-jmart-cabinet-layout',
  templateUrl: './jmart-cabinet-layout.component.html',
  styleUrls: ['./jmart-cabinet-layout.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class JmartCabinetLayoutComponent extends AbstractJmartCabinetLayout implements OnInit, OnDestroy {

  constructor(
    protected authService: JmartAuthService,
    private router: Router,
  ) {
    super(authService);
  }

  logout() {
    this.authService.logout();
    this.router.navigate(['jmart-cabinet', 'auth']);
  }

}
